import * as React from "react";
import { GET_MEDIA_LIST } from "./graphql/query";
import { useQuery } from "@apollo/client";
import Card from "./card/Card";
import LoadingDots from "./common/LoadingDots";

interface ICard {
  mediaId: string;
  slug: string;
  mediaType: string;
  language?: string[];
  genres?: string[];
  studio?: string;
  year?: number;
  translators?: string[];
  dubbers?: string[];
  editors?: string[];
}

interface IFilters {
  language?: string;
  genres?: string;
  studio?: string;
  year?: number;
  status?: string;
  translators?: string;
  dubbers?: string;
  editors?: string;
  hidden?: boolean;
}

interface IShowCase {
  mediaType: string;
  filters?: IFilters;
  history: any;
}

export default function ShowCase({
  mediaType,
  filters = {},
  history,
}: IShowCase) {
  const DivRef = React.useRef<HTMLDivElement>(null);
  const [docs, setDocs] = React.useState<ICard[]>([]);

  const [pagination, setPagination] = React.useState(0);
  const [limit, setLimit] = React.useState(0);
  const { loading, error, data } = useQuery(GET_MEDIA_LIST, {
    variables: {
      offset: 0,
      limit,
      mediaType,
      filters: { hidden: false },
    },
    fetchPolicy: "cache-and-network",
  });

  React.useEffect(() => {
    let cleanupFunction = false;
    if (!cleanupFunction && !loading && data) {
      setPagination(docs.length < 10 ? docs.length : 10);
      if (data.getMediaList.totalDocs > data.getMediaList.docs.length) {
        setLimit(data.getMediaList.totalDocs);
      } else {
        setDocs(
          data.getMediaList.docs.filter((item: ICard) => {
            return Object.keys(filters).length === 0
              ? true
              : (filters.language &&
                  item.language &&
                  item.language.includes(filters.language)) ||
                  (filters.genres &&
                    item.genres &&
                    item.genres.includes(filters.genres)) ||
                  (filters.studio &&
                    item.studio &&
                    item.studio === filters.studio) ||
                  (filters.year && item.year && item.year === +filters.year) ||
                  (filters.translators &&
                    item.translators &&
                    item.translators.includes(filters.translators)) ||
                  (filters.dubbers &&
                    item.dubbers &&
                    item.dubbers.includes(filters.dubbers)) ||
                  (filters.editors &&
                    item.editors &&
                    item.editors.includes(filters.editors));
          })
        );
      }
    }
    return () => {
      cleanupFunction = true;
    };
  }, [data, loading, docs, filters]);

  React.useEffect(() => {
    console.log(docs);
  }, [docs]);

  if (error) return <p>Error :(</p>;

  const showMore = () => {
    if (data && pagination !== docs.length) {
      if (pagination + 10 < docs.length) {
        setPagination(pagination + 10);
      } else {
        setPagination(docs.length);
      }
    }
  };

  return data && docs.length ? (
    <div className="flex flex-wrap" ref={DivRef}>
      {docs.map(
        (doc: ICard, index) =>
          index < pagination && (
            <Card
              key={doc.slug}
              mediaId={doc.mediaId}
              slug={doc.slug}
              mediaType={doc.mediaType}
              history={history}
            />
          )
      )}
      {data && pagination !== docs.length ? (
        <div
          className={`cursor-pointer flex flex-col my-4 justify-center items-center w-full h-18 text-white text-xl bg-indigo-900 hover:bg-indigo-400`}
          onClick={showMore}
        >
          <div>show more</div>
          <div>
            {pagination} from {docs.length}
          </div>
        </div>
      ) : (
        <div
          className={`flex flex-col my-4 justify-center items-center w-full h-18 text-white text-xl bg-indigo-900`}
        >
          <div>no more</div>
          <div>
            {pagination} from {docs.length}
          </div>
        </div>
      )}
      {pagination > 10 && (
        <div
          className="filter drop-shadow-lg fixed px-2 py-4 cursor-pointer right-1 bottom-12 text-white rounded-full bg-indigo-900 hover:bg-indigo-500"
          onClick={() => DivRef.current?.scrollIntoView()}
        >
          to UP
        </div>
      )}
    </div>
  ) : (
    <div className="text-white m-4">
      {loading ? <LoadingDots /> : !data ? "NOT FOUND" : null}
    </div>
  );
}
