import * as React from "react";
import "./App.css";
import ShowCase from "./ShowCase2";
import LastUpdate from "./LastUpdate2";
import Marks from "./Marks";
import Background from "./common/Background";
import history from "history/browser";
import queryString from "query-string";
import FilterTag from "./tags/FilterTag";

export default function App() {
  const [mediaType, setMediaType] = React.useState<string>(() => {
    if (history.location.pathname === "/avomova") {
      return "ALL";
    } else if (history.location.pathname === "/avomova/marks") {
      return "marks";
    } else {
      return history.location.pathname.split("/")[2];
    }
  });
  const [type, setType] = React.useState(() => {
    if (queryString.parse(history.location.search).type === "manga") {
      return "MANGA";
    } else if (queryString.parse(history.location.search).type === "dub") {
      return "DUB";
    } else if (queryString.parse(history.location.search).type === "sub") {
      return "SUB";
    } else {
      return "ALL";
    }
  });
  const [filters, setFilters] = React.useState(
    history.location.search === ""
      ? {}
      : queryString.parse(history.location.search)
  );

  React.useEffect(() => {
    const unlisten = history.listen(({ location, action }) => {
      if (location.pathname === "/avomova") {
        setMediaType("ALL");
      } else if (location.pathname === "/avomova/marks") {
        setMediaType("marks");
      } else {
        setMediaType(location.pathname.split("/")[2]);
      }

      if (queryString.parse(location.search).type === "manga") {
        setType("MANGA");
      } else if (queryString.parse(location.search).type === "dub") {
        setType("DUB");
      } else if (queryString.parse(location.search).type === "sub") {
        setType("SUB");
      } else {
        setType("ALL");
      }

      setFilters(
        location.search === "" ? {} : queryString.parse(location.search)
      );

      if (location.search === "?")
        history.replace({
          search: ``,
        });
    });
    return () => unlisten();
  });

  const handleShowCaseSettings = (type: string) => {
    if (type === "") {
      history.push({ pathname: `/avomova`, search: "" });
    } else {
      history.push({ pathname: `/avomova/${type}`, search: "" });
    }
  };

  return (
    <div className="App flex flex-col">
      <div className="text-white mb-2">
        based on{" "}
        <a className="hover:underline" href="https://anibel.net/">
          ANIBEL.NET
        </a>
      </div>
      <div className="text-white text-4xl font-bold p-2">&#9983; AVOMOVA</div>
      <div className="flex flex-wrap justify-end">
        <div
          onClick={() => handleShowCaseSettings("")}
          className={`transition ease-linear cursor-pointer text-white text-xl p-2 m-1 hover:bg-indigo-400 ${
            mediaType === "ALL" ? "bg-indigo-400" : "bg-indigo-900"
          }`}
        >
          last update
        </div>
        <div
          onClick={() => handleShowCaseSettings("anime")}
          className={`transition ease-linear cursor-pointer text-white text-xl p-2 m-1 hover:bg-indigo-400 ${
            mediaType === "anime" ? "bg-indigo-400" : "bg-indigo-900"
          }`}
        >
          anime
        </div>
        <div
          onClick={() => handleShowCaseSettings("manga")}
          className={`transition ease-linear cursor-pointer text-white text-xl p-2 m-1 hover:bg-indigo-400 ${
            mediaType === "manga" ? "bg-indigo-400" : "bg-indigo-900"
          }`}
        >
          manga
        </div>
        <div
          onClick={() => handleShowCaseSettings("cinema")}
          className={`transition ease-linear cursor-pointer text-white text-xl p-2 m-1 hover:bg-indigo-400 ${
            mediaType === "cinema" ? "bg-indigo-400" : "bg-indigo-900"
          }`}
        >
          cinema
        </div>
        <div
          onClick={() => handleShowCaseSettings("marks")}
          className={`transition ease-linear cursor-pointer text-white text-xl p-2 m-1 hover:bg-indigo-400 ${
            mediaType === "marks" ? "bg-indigo-400" : "bg-indigo-900"
          }`}
        >
          marks
        </div>
      </div>
      {history.location.search && (
        <div className="fixed right-1 top-1/4 flex-col text-white">
          <div>Filters:</div>
          {history.location.search
            .slice(1)
            .split("&")
            .map((item: string) => {
              return (
                item && (
                  <FilterTag
                    key={item}
                    tag={item.split("=")[0]}
                    value={item.split("=")[1]}
                    history={history}
                  />
                )
              );
            })}
        </div>
      )}
      <div>
        {mediaType === "ALL" ? (
          <LastUpdate type={type} history={history} />
        ) : mediaType === "marks" ? (
          <Marks history={history} />
        ) : (
          <ShowCase mediaType={mediaType} filters={filters} history={history} />
        )}
      </div>
      <Background />
    </div>
  );
}
