interface ITitle {
  be: string;
  ru: string;
  en: string;
  alt: string;
}

interface ITitleCard {
  title: ITitle;
}

const style = `
px-1
text-sm 
text-gray-800 
text-left
italic
leading-5
`;

export default function TitleEn({ title }: ITitleCard) {
  return <div className={style}>{title.en}</div>;
}
