interface ITitle {
  be: string;
  ru: string;
  en: string;
  alt: string;
}

interface ITitleCard {
  title: ITitle;
}

const style = `
  px-1
  text-xl
  text-indigo-900
  text-left
  leading-5
  `;

export default function Title({ title }: ITitleCard) {
  return <div className={style}>{title.be}</div>;
}
