interface IDescription {
  description: {
    be: string;
    ru: string;
    en: string;
  };
}

const style = `
flex
flex-col
py-1
px-2 
text-base 
text-black
text-left 
leading-5
`;

export default function Description({ description }: IDescription) {
  return (
    <div className={style}>
      <div className="text-sm text-gray-700 text-left">Description:</div>
      <div>{description.be}</div>
    </div>
  );
}
