import * as React from "react";
import { GET_UPDATES_LIST } from "./graphql/query";
import { useQuery } from "@apollo/client";
import CardLastUpdate from "./card/CardLastUpdate";
import LoadingDots from "./common/LoadingDots";

interface ICardLastUpdate {
  url: string;
  type: string;
  duraction: {
    start: number;
    end: number;
  };
  date: {
    created: Date;
    updated: Date;
  };
}

interface ILastUpdate {
  type: string;
  history: any;
}

export default function LastUpdate({ type, history }: ILastUpdate) {
  const DivRef = React.useRef<HTMLDivElement>(null);
  const [docs, setDocs] = React.useState<ICardLastUpdate[]>([]);
  const [pagination, setPagination] = React.useState(0);
  const [limit, setLimit] = React.useState(0);

  const { loading, error, data } = useQuery(GET_UPDATES_LIST, {
    variables: {
      offset: 0,
      limit,
      type: "ALL",
    },
    fetchPolicy: "cache-and-network",
  });

  React.useEffect(() => {
    let cleanupFunction = false;
    if (!cleanupFunction && !loading && data) {
      setPagination(docs.length < 10 ? docs.length : 10);
      if (data.getUpdatesList.totalDocs > data.getUpdatesList.docs.length) {
        setLimit(data.getUpdatesList.totalDocs);
      } else {
        setDocs(
          data.getUpdatesList.docs.filter((item: ICardLastUpdate) =>
            type === "ALL" ? true : item.type === type
          )
        );
      }
    }
    return () => {
      cleanupFunction = true;
    };
  }, [data, loading, docs, type]);

  React.useEffect(() => {
    console.log(docs);
  }, [docs]);

  if (error) return <p>Error :(</p>;

  const showMore = () => {
    if (data && pagination !== docs.length) {
      if (pagination + 10 < docs.length) {
        setPagination(pagination + 10);
      } else {
        setPagination(docs.length);
      }
    }
  };

  const handleType = (type: string) => {
    if (type === "ALL") {
      history.push({ pathname: `${history.location.pathname}`, search: "" });
    } else {
      history.push({
        pathname: `${history.location.pathname}`,
        search: `?type=${type}`,
      });
    }
  };

  return data && docs.length ? (
    <div className="flex flex-col" ref={DivRef}>
      <div className="flex flex-wrap">
        <div
          onClick={() => handleType("ALL")}
          className={`transition ease-linear cursor-pointer text-white text-xl p-2 m-1 hover:bg-indigo-400 ${
            type === "ALL" ? "bg-indigo-400" : "bg-indigo-900"
          }`}
        >
          ALL
        </div>
        <div
          onClick={() => handleType("dub")}
          className={`transition ease-linear cursor-pointer text-white text-xl p-2 m-1 hover:bg-indigo-400 ${
            type === "DUB" ? "bg-indigo-400" : "bg-indigo-900"
          }`}
        >
          DUB
        </div>
        <div
          onClick={() => handleType("sub")}
          className={`transition ease-linear cursor-pointer text-white text-xl p-2 m-1 hover:bg-indigo-400 ${
            type === "SUB" ? "bg-indigo-400" : "bg-indigo-900"
          }`}
        >
          SUB
        </div>
        <div
          onClick={() => handleType("manga")}
          className={`transition ease-linear cursor-pointer text-white text-xl p-2 m-1 hover:bg-indigo-400 ${
            type === "MANGA" ? "bg-indigo-400" : "bg-indigo-900"
          }`}
        >
          MANGA
        </div>
      </div>
      <div className="flex flex-wrap">
        {docs.map(
          (doc: ICardLastUpdate, index) =>
            index < pagination && (
              <CardLastUpdate
                key={doc.url + doc.date.updated}
                url={doc.url}
                type={doc.type}
                duraction={doc.duraction}
                date={doc.date}
                history={history}
              />
            )
        )}
        {data && pagination !== docs.length ? (
          <div
            className={`cursor-pointer flex flex-col my-4 justify-center items-center w-full h-18 text-white text-xl bg-indigo-900 hover:bg-indigo-400`}
            onClick={showMore}
          >
            <div>show more</div>
            <div>
              {pagination} from {docs.length}
            </div>
          </div>
        ) : (
          <div
            className={`flex flex-col my-4 justify-center items-center w-full h-18 text-white text-xl bg-indigo-900`}
          >
            <div>no more</div>
            <div>
              {pagination} from {docs.length}
            </div>
          </div>
        )}
      </div>
      {pagination > 10 && (
        <div
          className="filter drop-shadow-lg fixed px-2 py-4 cursor-pointer right-1 bottom-24 text-white rounded-full bg-indigo-900 hover:bg-indigo-500"
          onClick={() => DivRef.current?.scrollIntoView()}
        >
          to UP
        </div>
      )}
    </div>
  ) : (
    <div className="text-white m-4">
      {loading ? <LoadingDots /> : !data ? "NOT FOUND" : null}
    </div>
  );
}
