import * as React from "react";
import Card from "./card/Card";
import { useLocalStorage } from "./useLocalStorage";

interface IMark {
  slug: string;
  mediaType: string;
}

interface IMarks {
  history: any;
}

export default function Marks({ history }: IMarks) {
  const [marksStore] = useLocalStorage("marks", []);

  return marksStore.length ? (
    <div className="flex flex-wrap">
      {marksStore.map((mark: IMark, index: number) => (
        <Card
          key={mark.slug}
          mediaId={index}
          slug={mark.slug}
          mediaType={mark.mediaType}
          history={history}
        />
      ))}
    </div>
  ) : (
    <div className="text-white m-4">NOT FOUND</div>
  );
}
